body, html {
  line-height: 1.5;
}

h1, h2, h3, h4, h5, h6, ul, .card-title, p {
  margin-bottom: 0;
}

ul, ol {
  list-style: none;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position:center
}

a:hover {
  text-decoration: none;
}


small {
  font-size: 14px;
}

.bg-cover {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.rectangle {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

}





